:root {
  --color-primary: rgb(23, 133, 236);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  min-height: 100vh;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  font-weight: inherit;
}

.navbar {
  /* background-color: aqua; */
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  padding: 0 1.5rem;
  background-color: #fff;
  box-shadow: 0 -5px 10px 2px #000;
  height: 8rem;
  z-index: 9999;
}
.navbar-logo {
  margin-left: 2rem;
  font-size: 3rem;
  font-weight: 500;
  letter-spacing: 1px;
}

.NFT {
  color: var(--color-primary);
  font-weight: 800;
  letter-spacing: 3px;
  position: relative;
}

.navbar-nav {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 2rem;
}

.navbar-nav-item {
  font-size: 2rem;
  padding: 2.5rem 2rem;
  font-weight: 500;
}

.navbar-nav-item:hover {
  cursor: pointer;
}

.navbar-nav-link {
  width: 100%;
  height: 100%;
}

.navbar-nav-item.active {
  color: var(--color-primary);
}


@media only screen and (max-width:500px) {
  .navbar {
    padding: 0;
    justify-content: space-between;
  }
  .navbar-logo {
    font-size: 2.5rem;
  }

  .navbar-nav {
    margin: 0;
    margin-right: 1rem;
  }

  .navbar-nav-item {
    padding: .5rem 1rem;
  }

  .navbar-nav-link {
    font-size: 2.5rem;
  }
}